import React, {useState} from 'react'
import PersonPinIcon from '@material-ui/icons/PersonPin';


 const Login = (props) => {
    const [loginUser ,setLoginUser] = useState('');
    const [loginPassword, setLoginPassword] = useState('');


    console.log(loginUser)
    return (
     <div className= "TopLoginContainer"style={ props.loggedIn ? { display:'none'} : {display : 'block'}} >
        <div className='LoginContainer'  style={ props.loginClick ? { display:'none'} : {display : 'block'}} >
            <ul className="loginNav">
            <form className="login"  >
                <PersonPinIcon className= "personicon" />
                <h5>Name</h5>
                <input type="text"   placeholder="JiuJitsu" onChange={(e) => setLoginUser(e.target.value)}/>
                <h5>Password</h5>
                <input type="password"  value="Roll01234" />
                <br/>  
                <input type="submit" onClick={function tha(e) {e.preventDefault(); props.submitLogin()}}/>
             </form>
            </ul>
        </div>
     </div>
    )   
}

export default Login