
import React, {useState, useContext} from 'react'
import VideoModal from './VideoModal'
import { makeStyles } from '@material-ui/core/styles';
import TreeView from '@material-ui/lab/TreeView';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import TreeItem from '@material-ui/lab/TreeItem';
import { blue } from '@material-ui/core/colors';
import { UserContext } from './UserContext';
import CancelIcon from '@material-ui/icons/Cancel';

const useStyles = makeStyles({
  root: {
    height: 240,
    flexGrow: 1,
    maxWidth: 400,
    color:blue,
    padding:10,
    marginTop: 20,
    marginLeft:15
  
  },
  
});




const data = {
  id: 'root',
  name: 'Technique',
  children: [

    {
      id: 2,
      name: 'Standing',
      children: [
        {
          id: 3,
          name:  'Double Leg'
      
        },
        {
          id: 4,
          name: 'Single',
        },
      ],
    },

    {
      id: 5,
      name: 'Guard',
      children: [
        {
          id: 6,
          name: 'Armbar',
        },
      ],
    },
    {
      id: 7,
      name: 'Passing',
      children: [
        {
          id: 8,
          name: 'Knee Cut',
        },
      ],
    },

    {
      id: 8,
      name: 'Top',
      children: [
        {
          id: 9,
          name: 'Armbar',
        },
        {
          id: 10,
          name: 'Kimura',
        },
      ],
    },


  ],}



function Techniques(props) {

 const classes = useStyles();
 const [tData, setTData]= useState(data)
 const [modal, setModal]= useState(false)
 const [delMode, setDelMode]= useState(false)
 const [modalLink, setModalLink]= useState('')
 let link = JSON.stringify(props.fileName );
 let link2 = link.slice(1,-1);
 const {loggedIn, setLoggedIn}  = useContext(UserContext);
 console.log(data)



if( props.submitClk === true) {

  data.children[props.treePath].children.push({id: (Math.floor(Math.random() * 1000)), name: <a className="link" id={(Math.floor(Math.random() * 1000))} onClick={setModal2} > {props.techName} </a> , link: <a> X</a>})
  let vidLink = 'http://localhost:5000';
  let finLink = vidLink.concat('/', link2)



function setModal2(e){

  
 console.log(e.target)
  setModal(true)
  setModalLink(finLink)

}



  props.setSubmitClk(false);
  console.log(finLink)


}

  const renderTree = (nodes) => (
    <TreeItem  key={nodes.id} nodeId={nodes.id} label={nodes.name} > 
      {Array.isArray(nodes.children) ? nodes.children.map((node) => renderTree(node) ) : null}
      
    </TreeItem>
  
  );
  function deleteMode(e){
    setDelMode(prevDel => !prevDel);
    console.log(delMode)
 
  }

  return ( 
    <div className="treeView" 
    style={loggedIn ? {display:'block'} :{display:'none'}}
    
    >
    <TreeView  
     
      className={classes.root}
      defaultCollapseIcon={<ExpandMoreIcon />}
      defaultExpanded={['root']}
      defaultExpandIcon={<ChevronRightIcon />}  >
      
      {renderTree(data)}
    </TreeView>
   
    <VideoModal modalLink={modalLink} modal={modal} setModal={setModal}/>
    <CancelIcon style={{fontSize:'55', left:'10', top:'46.1vh', position:'absolute'}}/>

    </div>
  );
}






export default Techniques

