import React , {useContext} from 'react'
import ll from "./ll.mp4"; 
import { UserContext } from './UserContext';


const Video = () => { 
   const {loggedIn, setLoggedIn}  = useContext(UserContext);


return (
    <div className="Main" style={loggedIn ? {display:'block'} :{display:'none'}}>
      <h3 className="vdb">Your Gym's Virtual Online Database</h3>
      <iframe className="one" width="560" height="315" src="https://www.youtube.com/embed/G29bme3msUw" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
      <video className="two" controls  src={ll} type="video/mp4" />
      <video className="three" controls  src={'http://localhost:5000/rh.mp4'} type="video/mp4" />
      <iframe className="four" width="420" height="300"   controls src="https://www.youtube.com/embed/d9tMyhNgec4" 
      frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
      <iframe className="five" width="420" height="300"  controls src="https://www.youtube.com/embed/55Ux8giP_PY" frameborder="0" allow="accelerometer; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
      <iframe className="six" width="420" height="300"  controls src="https://www.youtube.com/embed/P5SeCRensWQ" frameborder="0" allow="accelerometer; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>

      

</div>

)}

export default Video
