import React, {useContext} from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import { UserContext } from './UserContext';
import ImgMediaCard from './Card.js'


function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`nav-tabpanel-${index}`}
      aria-labelledby={`nav-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box p={3}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `nav-tab-${index}`,
    'aria-controls': `nav-tabpanel-${index}`,
  };
}

function LinkTab(props) {
  return (
    <Tab
      component="a"
      onClick={(event) => {
        event.preventDefault();
      }}
      {...props}
    />
  );
}

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    backgroundColor: theme.palette.background.paper,
  },
}));

export default function NavTabs() {
  const classes = useStyles();
  const [value, setValue] = React.useState(0);  
  const {loggedIn, setLoggedIn}  = useContext(UserContext);
  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <div className={classes.root}  style={{ display : loggedIn ? 'none' : 'block'}}
    >
      <AppBar position="relative" 
      style={{top:'30vh',width:'40vw', left:'28vw', backgroundColor:' rgb(3, 161, 235)', zIndex:'1', borderColor:'black', borderStyle:'solid'}}>
        <Tabs
          variant="fullWidth"
          value={value}
          onChange={handleChange}
          aria-label="nav tabs example"
        >
          <LinkTab label="Page One" href="/drafts" {...a11yProps(0)} />
          <LinkTab label="Page Two" href="/trash" {...a11yProps(1)} />
          <LinkTab label="Page Three" href="/spam" {...a11yProps(2)} />
        </Tabs>
      </AppBar>
      <TabPanel  style={{ backgroundColor:'black',width:'70vw', left:'13vw', position:"relative", top:'30vh' }} value={value} index={0}>
     
        <ImgMediaCard />
      </TabPanel>
      <TabPanel style={{ backgroundColor:'black',width:'70vw', left:'13vw', position:"relative", top:'30vh' }} value={value} index={1}>
      
        <ImgMediaCard  />
      </TabPanel>
      <TabPanel style={{ backgroundColor:' rgb(3, 161, 235)',width:'70vw', left:'13vw', position:"relative", top:'30vh' }} value={value} index={2}>
       
        <ImgMediaCard />
      </TabPanel>
    </div>
  );
}
