import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import Hero5 from './Hero5.png'

const useStyles = makeStyles({
  root: {
    maxWidth: 445,
    height: 550,
  },
});

 function ImgMediaCard() {
  const classes = useStyles();

  return (
    <div>
    <Card className={classes.root} >
      <CardActionArea >
        <CardMedia
          component="img"
          alt="Contemplative Reptile"
          height="340"
          width="340"
          image={Hero5}
          title="Reel/Roll Personalized Technique Repository"
        />
        <CardContent >
          <Typography gutterBottom variant="h5" component="h2">
          "Reel/Roll Personalized Technique Repository"
          </Typography>
          <Typography variant="body2" color="textSecondary" component="p">
           Bring your academy into the virtual realm. Easy to update database will keep your team focused around your vison. 
          </Typography>
        </CardContent>
      </CardActionArea>
      <CardActions>
        <Button size="small" color="primary">
          Share
        </Button>
        <Button size="small" color="primary">
          Learn More
        </Button>
      </CardActions>
    </Card>
      <div className="slogan3">
        <h1 >Give Your Team The Advantage </h1>
           <ul>
            <h2>Record and post your instructionals</h2>
            <li>Record and post your instructionals</li>
           
            <h2>Easily Accesible By Your Students</h2>
            <li>Record and post your instructionals</li>
            <h2>Lightweight and Affordable</h2>
            <li>Record and post your instructionals</li>
           </ul>
        </div>
    </div>
  );
}
export default ImgMediaCard