import React, { useState, useEffect, useContext } from 'react'
import axios from 'axios' ;
import Techniques from './TechniqueList';
import Message from './Message'; 
import { UserContext } from './UserContext';
import AddCircleIcon from '@material-ui/icons/AddCircle';


 const FileUpload = (props) => {
  const[file, setFile] = useState();
  const [fileName, setFilename] = useState('Upload Video');
  const [uploadedFile ,setUploadedFile] = useState({});
  const [message ,setMessage] = useState('');
  const [treePath, setTreePath] = useState('0');
  const [ techName, setTechName] = useState('  ');
  const [ techLink, setTechLink] = useState('  ');
  const [ submitClk, setSubmitClk] = useState(false);
  const [ uploadModal, setUploadModal] = useState(false);
  const {loggedIn, setLoggedIn}  = useContext(UserContext);


  const onChange = e => {
    e.preventDefault();
    e.stopPropagation();
    setFile(e.target.files[0]);
    setFilename(e.target.files[0].name);
   
  };



  const fileChange = e => {
    setFilename(e.target.innerHTML)
  }
  
  const onSubmit = async e => {
    e.preventDefault();
    e.stopPropagation();
    const formData = new FormData();
    formData.append('file', file);

    try{
      const res = await axios.post('http://localhost:5000/upload', formData, {   
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      });

      const { fileName, filePath } = res.data;
  
      setUploadedFile( { fileName, filePath})
      setMessage('File Uploaded');
      setTechLink(uploadedFile.filePath)
      setSubmitClk(true);
     
    } catch (err) {
      if (err === 500) {
        setMessage('There was an issue with the server')
      } else {
        setMessage('File Submitted')
      }
    }
  }
  useEffect(() => {
    setUploadedFile('')
    setTechLink('')
    setTreePath(0);
    setFilename('')
    setFile('')
    setTechName(' ');
  }, [submitClk]); 




  return (
    <>
    <div className="Upload" style={uploadModal ? {display:'block'} :{display:'none'}}>

      <form className="uploadform">
        
        <div className='search'>
          <input type='file' className='custom-file-input' id='customFile' 
          onChange={onChange}
          />
          <label  className='custom-file-label' htmlFor='customFile' onChange={fileChange}>
          
          </label>
          <input  className="formBut" type="button" value="Upload"  onClick={onSubmit} /> 
        </div>

        </form>    
            
        <div className="techniqueupload">
       
        <form className="drop" >
            <select className= "select" id="cars" name="cars" onChange={e => setTreePath(e.currentTarget.value)}> 
              <option value="0">Standing</option>
              <option value="1">Guard</option>
              <option value="2">Passing</option>
              <option value="3">Top</option>
              <option value="4">Back</option>      
            </select>
            <input type="text" htmlFor = "newValue" placeholder = "Label your video" value = {submitClk ? '' : techName} onChange={e => setTechName(e.currentTarget.value)}             
          /><span className="techname">Technique name</span><span className="positionname">Position</span>
       </form>
       {message ? <Message msg={message} /> :null}

<Techniques treePath={treePath} submitClk={submitClk}
setSubmitClk={setSubmitClk}
 techName={techName} fileName={fileName} techLink={techLink} />


     </div>

        </div>
             <AddCircleIcon style={{fontSize: '55', position:'absolute', top:'83.1vh', left:'93.8vw', color:'#1a9cd8', display:'block', zIndex:'1'}} onClick={() => setUploadModal(true)}/>
             </>
  )
}

export default FileUpload