import React from 'react'

const VideoModal = (props) => {


    return (
        <div className="videomodal"  style={{
            display: props.modal ? "block": "none",
        
          }}   >
               <h3 className="company3">REEL / ROLL</h3><span className="close" onClick={()=> {props.setModal(false)}}>X</span>
            <video className="vidPlayer" controls src={props.modalLink} type="video/mp4" width="1620" height="1620"/>
        </div>
    )
}

export default VideoModal